import BaseModule from './BaseModule'
export default class HandleCache extends BaseModule {

  register() {
    const videos = document.querySelectorAll('video source');

    videos.forEach(video => {
      fetch(video.src).then(async response => {
        console.log('fetch response', response);

        if (response.ok) {
          await this.addCache(response.url)

          this.checkVideoCache(response.url).then(isCached => {
            if (isCached) {
              console.log('Video is cached', response.url);
              this.setUnlimitCache(response.url)
            } else {
              console.log('Video is not cached', response.url);
            }
          }).catch(error => {
            console.error('Error checking video cache:', error, response.url);
          });

        }
      });
    });
  }

  addCache(url) {
    return caches.open('my-cache')
      .then(cache => {
        return cache.add(url);
      });
  }

  checkVideoCache(url) {
    return caches.match(url)
      .then(response => {
        if (response) {
          return true;
        } else {
          return false;
        }
      });
  }

  async setUnlimitCache(url) {
    const cacheStorage = await window.caches.open('my-cache');
    const cacheStorageOptions = await cacheStorage.match(url);

    cacheStorageOptions.maxSize = 0;
    console.log('cacheStorageOptions', cacheStorageOptions);
    console.log('Cache storage size limit set to unlimited:', await cacheStorage.match(url));
  }
}
