import BaseModule from './BaseModule'
export default class HandleShowTime extends BaseModule {

  register() {
    this.isInteracted = false
    this.trackTimeout = 0
    this.videoBg = document.querySelector('#video-bg')
    this.leader = this.el.querySelector('.slide')
    this.leaderTime = ~~(this.leader.getAttribute('data-auto')) || 120000
    this.leaderLimit = ~~(this.leader.getAttribute('data-limit')) || 120000

    this.intro = this.el.querySelector('.video-intro')
    this.introVideo = this.intro?.querySelector('video')
    this.introTime = ~~(this.introVideo?.getAttribute('data-limit'))

    this.rwe = this.el.querySelector('.video-rwe')
    this.rweVideo = this.rwe?.querySelector('video')
    this.rweTime = ~~(this.rweVideo?.getAttribute('data-limit'))

    const leaderboard = [{ item: this.leader, time: this.leaderLimit }]
    const introSet = this.intro
      && [...leaderboard, { item: this.intro, time: this.introTime * 1000, video: this.introVideo }]
      || leaderboard
    const rweSet = this.rwe
      && [...leaderboard, { item: this.rwe, time: this.rweTime * 1000, video: this.rweVideo }]
      || []

    this.slides = [
      ...introSet,
      ...rweSet
    ]
    this.idxCurrent = 0

    // interact to start slide
    this.leader.addEventListener('click', (e) => {
      if (!this.isInteracted) {
        this.isInteracted = true
        this.leader.classList.add('leaderboard--update')
        this.leader.removeAttribute('style')
        this.playNextSlide()
        this.videoBg.play()
      }
    });
  }

  playNextSlide() {
    this.handleSlideChanged()

    if (this.current.video) {
      this.handleCurrentVideo(this.current)
    } else {
      this.handleLeaderboard()
    }
  }

  handleSlideChanged() {
    // clear previous activity
    const prev = (this.idxCurrent - 1) >= 0
      ? this.slides[this.idxCurrent - 1]
      : this.slides[this.slides.length - 1]

    prev.item?.classList.remove('slide-active')

    // set current active
    this.current = this.slides[this.idxCurrent]
    this.current.item?.classList.add('slide-active')
  }

  handleLeaderboard() {
    const leaderInterval = setInterval(() => {
      if (this.leader.classList.contains('leaderboard--update')) {
        this.leader.classList.remove('leaderboard--update')
      } else {
        this.leader.classList.add('leaderboard--update')
      }
    }, this.leaderTime)

    const leaderTimeout = setTimeout(() => {
      clearInterval(leaderInterval)
      clearTimeout(leaderTimeout)
      this.idxCurrent = (this.idxCurrent + 1) % this.slides.length
      this.playNextSlide()
    }, this.leaderLimit);
  }

  handleCurrentVideo(current) {
    const currentVideo = current.video

    if (currentVideo) {
      this.addVideoEvent(current)
      currentVideo.muted = false
      currentVideo.currentTime = 0

      const promise = currentVideo.play();
      if (promise !== undefined) {
        promise.then(() => {
          this.isInteracted = true
        }).catch(error => {
          currentVideo.addEventListener('click', (e) => {
            this.isInteracted = true
            currentVideo.play()
          });
        });
      }
    }
  }

  addVideoEvent(current) {
    current.video.ontimeupdate = () => { this.handleTimeupdate(current) };
    current.video.onended = () => { this.handleVideoEnd(current) };
  }

  handleTimeupdate(current) {
    const currentVideo = current.video
    const videoTimeLimit = current.time || current.video.duration * 1000

    if (videoTimeLimit && ~~currentVideo.currentTime === ~~videoTimeLimit/1000) {
      this.handleVideoEnd(current)
    }

    if (videoTimeLimit && ~~currentVideo.currentTime === (~~videoTimeLimit/1000 - 2)) {
      this.soundDown(current.video)
    }
  }

  handleVideoEnd(current) {
    const currentVideo = current.video

    if (currentVideo) {
      currentVideo.pause();
      currentVideo.currentTime = 0;
      this.idxCurrent = (this.idxCurrent + 1) % this.slides.length
      this.playNextSlide()
    }
  }

  soundDown(video) {
    const intervalSoundDown = setInterval(() => {
      if (video?.volume > 0.1) {
        video.volume -= 0.1;
      } else {
        clearInterval(intervalSoundDown);
      }
    }, 200);

    video.onpause = () => {
      clearInterval(intervalSoundDown);
      video.volume = 1
    };
  }
}
